import { useContext, useState } from 'react';
import { Collapse } from 'reactstrap';
import CollapseContext from '../../contexts/collapse.context';
import { submitForm } from '../../firebase/firebase.utils';

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    message: ''
  });
  const { name, email, message } = formData;
  const onChange = e =>
    setFormData({ ...formData, [e.target.name]: e.target.value });
  const onSubmit = e => {
    e.preventDefault();
    submitForm(formData);
    console.log(formData);
  };
  const { collapses } = useContext(CollapseContext);

  return (
    <Collapse isOpen={!collapses.contact}>
      <div id="contact">
        <div className="card card-body bg-danger text-white py-4 px-5">
          <h2>Contact</h2>
        </div>

        <div className="card card-body py-4 px-5">
          <h3 className="mb-4">Get In Touch With Me</h3>
          <div className="alert">Your message has been sent</div>

          <form id="contactForm" onSubmit={e => onSubmit(e)}>
            <div className="form-group">
              <div className="input-group input-group-lg">
                <div className="input-group-prepend">
                  <span className="input-group-text bg-danger text-white">
                    <i className="fas fa-user"></i>
                  </span>
                </div>
                <input
                  type="text"
                  name="name"
                  placeholder="Name"
                  value={name}
                  onChange={e => onChange(e)}
                  className="form-control bg-dark text-white"
                  required
                />
              </div>
            </div>

            <div className="form-group">
              <div className="input-group input-group-lg">
                <div className="input-group-prepend">
                  <span className="input-group-text bg-danger text-white">
                    <i className="fas fa-envelope"></i>
                  </span>
                </div>
                <input
                  type="email"
                  name="email"
                  placeholder="Email"
                  value={email}
                  onChange={e => onChange(e)}
                  className="form-control bg-dark text-white"
                  required
                />
              </div>
            </div>

            <div className="form-group">
              <div className="input-group input-group-lg">
                <div className="input-group-prepend">
                  <span className="input-group-text bg-danger text-white">
                    <i className="fas fa-pencil-alt"></i>
                  </span>
                </div>
                <textarea
                  type="text"
                  placeholder="Message"
                  name="message"
                  value={message}
                  onChange={e => onChange(e)}
                  className="form-control bg-dark text-white"
                ></textarea>
              </div>
            </div>

            <div className="d-flex">
              <input
                type="submit"
                value="Submit"
                className="btn btn-danger btn-lg ml-auto"
              />
            </div>
          </form>
        </div>
      </div>
      <style jsx>
        {`
          .alert {
            text-align: center;
            padding: 10px;
            background: #79c879;
            color: #fff;
            margin-bottom: 10px;
            display: none;
          }
        `}
      </style>
    </Collapse>
  );
};

export default Contact;
